import React, { useEffect, useState } from 'react';
import Lectorem from '../../package/Lectorem/Lectorem';
import './Story.css';

const audioPath = 'data/stories/burro/audio.mp3';
const textPath = 'data/stories/burro/text.json';

const Story = () => {
  const [textData, setTextData] = useState();
  const onMount = () => {
    console.log('mounting');
    fetch(textPath)
      .then(d => d.json())
      .then(d => setTextData(d))
      .catch(e => console.error(e));
  };

  useEffect(onMount, []);

  return (
    <div className='Story'>
      <Lectorem
        audioPath={ audioPath }
        textData={ textData }
      />
    </div>
  );
};

export default Story;