import React, { useState } from 'react';
import Lect from './Lect/Lect';
import Player from './Player/Player';
import './Lectorem.css';

const renderLect = (lect, idx, readTime) => {
  const shouldHighlight = lect.start <= readTime && readTime <= lect.end;
  const key = shouldHighlight ? readTime : idx;
  return <Lect lect={ lect } shouldHighlight={ shouldHighlight } key={ key } readTime={ readTime } />
};

const renderParagraphs = (paragraphs, readTime) => {
  console.log('here1', paragraphs);
  return paragraphs.map(
    (paragraph, idx) => (
      <div className='paragraph' key={ idx }>
        { paragraph.lines.map((lect, idx) => renderLect(lect, idx, readTime)) }
      </div>
    )
  );
}

const Lectorem = ({ textData, audioPath }) => {
  const [readTime, setReadTime] = useState(0);
  if (!textData) {
    return <p>Loading...</p>;
  }
  const { title, subtitle } = textData;
  return (
    <div className='Lectorem'>
      { !!title && <h2>{ title } </h2> }
      { !!subtitle && <h3>{ subtitle } </h3> }
      <div className='playerContainer'>
        <Player
          audioPath={ audioPath }
          onListen={ setReadTime }
        />
      </div>
      <div className='lectsContainer'>
        { renderParagraphs(textData.paragraphs, readTime) }
      </div>
    </div>
  );
}

export default Lectorem;
